import React, { useState } from "react";
import { useNavigate } from "react-router-dom";
import axios from "axios";
import "../assets/styles/SearchCard.css";

const SearchCard = () => {
  const [text, setText] = useState("");
  const [loading, setLoading] = useState(false); // ë¡œë”© ìƒíƒœ ì¶”ê°€
  const navigate = useNavigate();

  const sendClicked = async () => {
    if (!text.trim()) {
      console.warn("ì…ë ¥ê°’ì´ ë¹„ì–´ìˆìŠµë‹ˆë‹¤.");
      return;
    }

    setLoading(true); // API ìš”ì²­ ì‹œì‘ ì‹œ ë¡œë”© ìƒíƒœ í™œì„±í™”

    const isProduction = window.location.protocol === "https:";
    const backUrl = isProduction 
      ? process.env.REACT_APP_BACKEND_URL_PROD  // HTTPS (CloudFront ë°°í¬ í™˜ê²½)
      : process.env.REACT_APP_BACKEND_URL;     // HTTP (ë¡œì»¬/S3)
    
    console.log("Using API URL:", backUrl);
    
    const url = `${backUrl}/recommend?prompt=${encodeURIComponent(text)}`;

    try {
      const response = await axios.get(url, {
        headers: {
          "Content-Type": "application/json",
        },
      });

      console.log("Response Data:", response.data);
      navigate("/main", { state: { recommendation: response.data } });
    } catch (error) {
      console.error("Error fetching recommendation:", error);
    } finally {
      setLoading(false); // API ìš”ì²­ ì™„ë£Œ ì‹œ ë¡œë”© ìƒíƒœ í•´ì œ
    }
  };

  return (
    <div className="search-card-container">
      <div className="search-card">
        <form onSubmit={(e) => e.preventDefault()}>
          <h2 className="search-title">ë„ˆì— ëŒ€í•´ì„œ ì•Œë ¤ì¤˜</h2>
          <div className="search-box">
            <textarea
              className="search-box-textbox"
              value={text}
              onChange={(e) => setText(e.target.value)}
            />
          </div>
        </form>
      </div>
      <button className="send-btn" onClick={sendClicked} disabled={loading}>
        {loading ? "ë¡œë”© ì¤‘..." : "ë³´ ë‚´ ê¸°"}
      </button>
    </div>
  );
};

export default SearchCard;
